<template>
  <div class="pdf_container">
    <div id="viewerContainer" class="viewerContainer" ref="containerRef">
      <div id="innerContainer" class="pdfViewer"></div>
    </div>
  </div>
</template>

<script>
import * as pdfjs from "pdfjs-dist/build/pdf";
import {
  PDFLinkService,
  PDFViewer,
  PDFSinglePageViewer,
  PDFFindController,
  EventBus,
  NullL10n,
} from "pdfjs-dist/web/pdf_viewer";
import "pdfjs-dist/web/pdf_viewer.css";
pdfjs.GlobalWorkerOptions.workerSrc =
  "https://cdnjs.cloudflare.com/ajax/libs/pdf.js/3.6.172/pdf.worker.js";
  // "/pdf.worker.js";
export default {
  name: "pdfComponent",
  data() {
    return {
      pdfLoaded: false,
      state: {},
      newViewer: null,
      eventBus: null,
      linkService: null,
      oldPageNum: 1,
    };
  },
  props: {
    url: {
      type: String,
      default: "",
    },
    scale: {
      type: [String, Number],
      default: "1",
    },
    textMode: {
      type: Number,
      default: 1,
    },
    singlePage: {
      type: Boolean,
      default: false,
    },
  },
  components: {},
  mounted() {
    this.initPdf(this.url);
  },
  methods: {
    /*PDF*/
    initPdf(url) {
      const that = this;
      that.state.numPages = 0;
      that.state.curPageNum = 0;

      const eventBus = new EventBus();
      const linkService = new PDFLinkService();
      that.eventBus = eventBus;
      that.linkService = linkService;

      const findController = new PDFFindController({
        linkService,
        eventBus,
        updateMatchesCountOnProgress: true,
      });

      if (that.singlePage) {
        that.newViewer = new PDFSinglePageViewer({
          container: document.getElementById('viewerContainer'), // PDFdom
          linkService,
          useOnlyCssZoom: true, // css, false
          textLayerMode: that.textMode, //    0
          findController, // 
          eventBus,
          l10n: NullL10n,
        });
      } else {
        that.newViewer = new PDFViewer({
          container: document.getElementById('viewerContainer'), // PDFdom
          linkService,
          useOnlyCssZoom: true, // css, false
          textLayerMode: that.textMode, //    0
          findController, // 
          eventBus,
          l10n: NullL10n,
        });
      }

      linkService.setViewer(that.newViewer);

      eventBus.on("pagesinit", function () {
        // 
        that.newViewer.currentScaleValue = that.scale;
      });

      //  find 
      eventBus.on('find', function () {
        // 
        //console.log('find',e)
      });


      pdfjs
        .getDocument({
          url: url,
          cMapPacked: true,
          cMapUrl: "https://cdn.jsdelivr.net/npm/pdfjs-dist@3.6.172/cmaps/",
        })
        .promise.then((pdf) => {
          //console.log("pdf", pdf);
          if (pdf) {
            that.pdfLoaded = true;
            // 
            that.state.numPages = pdf.numPages;
            that.state.curPageNum = 1;
            //console.log(that.state);
            that.newViewer.setDocument(pdf);
            linkService.setDocument(pdf);
            this.loaded();

            //
            document
              .getElementById("viewerContainer")
              .addEventListener("scroll", function () {
                that.state.curPageNum = that.newViewer.currentPageNumber;
                if (that.oldPageNum !== that.state.curPageNum) {
                  that.pageChange();
                  that.oldPageNum = that.state.curPageNum;
                }
              });
            // 
            that.getOutLine(pdf)
          }
        })
        .catch((err) => {
          console.log(err);
          that.state.numPages = 0;
          that.state.curPageNum = 0;
          that.pdfLoaded = false;
        });
    },
    loaded() {
      this.$emit("loaded", this.state, this.newViewer);
    },
    pageChange() {
      this.$emit("pageChange", this.state);
    },
    pageGo(pageNumber) {
      this.linkService.goToPage(pageNumber);
      this.state.curPageNum = this.newViewer.currentPageNumber;
      this.pageChange();
    },
    pdfFindAgain(keyWord) {
      this.eventBus.dispatch("find", {
        type: "again",
        query: keyWord,
        phraseSearch: true,
        caseSensitive: false, // 
        entireWord: false, // 
        // highlightAll: false, // 
        // findPrevious: undefined, // 
      });
    },
     pdfFindAll(keyWord) {
      this.eventBus.dispatch("find", {
        type: "",
        query: keyWord,
        phraseSearch: true,//
        caseSensitive: false, // 
        entireWord: false, // 
        highlightAll: true, // 
        findPrevious: undefined, // 
      });
    },
    getOutLine(pdf) {
      //
      pdf.getOutline().then( (res) => {
        //res
        if(res===null){
          this.$emit("getOutLine", res);
          return false
        }else{
          res.map(async r=>{
            return r.pageNumber = await pdf.getPageIndex(res[0].dest[0]) + 1
          })
          // console.log('outline', res)
          this.$emit("getOutLine", res);
        }
      
      
      })
      
    },
  },
};
</script>

<style scoped>
.viewerContainer {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0px;
  overflow: auto;
}
.viewerContainer::-webkit-scrollbar {
  width: 8px;
}
.viewerContainer::-webkit-scrollbar-thumb{
  background-color:#ddd;
}
</style>